// background mixins
@mixin bk-grad($color1, $color2 ,$color3) {
 
  /* Permalink - use to edit and share this gradient: https://colorzilla.com/gradient-editor/#5580e9+1,094ee5+100 */
background: $color1; /* Old browsers */
background: -moz-linear-gradient(45deg,  rgba($color1 , 1) 1%, rgba($color2 , 1) 50%, rgba($color3 , 1) 100%); /* FF3.6-15 */
background: -webkit-linear-gradient(45deg,  rgba($color1 , 1) 1%, rgba($color2 , 1) 50%, rgba($color3 , 1) 100%); /* Chrome10-25,Safari5.1-6 */
background: linear-gradient(45deg,  rgba($color1 , 1) 1%, rgba($color2 , 1) 50%, rgba($color3 , 1) 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
filter: progid:DXImageTransform.Microsoft.gradient( startColorstr=$color1, endColorstr=$color2,GradientType=1 ); /* IE6-9 fallback on horizontal gradient */

  }

  // quote animations
  @keyframes quote-anim-1 {
    from {
      transform: translateX(0%) scale(1);
      opacity: 1
    }
    to {
      transform: translateX(-100%) scale(0.7);
      opacity: 0
    }
  }

  @keyframes quote-anim-2 {
    from {
      transform: scale(0.7);
      opacity: 0
    }
    to {
      transform: scale(1);
      opacity: 1
    }
  }